.swi-petition-counter {
  position: relative;
  text-align: center;
}
.swi-petition-counter .swi-petition-counter-number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #000;
  font-weight: bold;
}
.swi-petition-counter svg {
  width: 140px;
  height: 140px;
  transform: rotate(-90deg);
  fill: none;
  stroke: #000;
  stroke-width: 10;
  stroke-dasharray: 260px;
  stroke-dashoffset: 0;
}